import { Grid, Paper, Table, TableContainer, TableHead, TableRow, TableCell, TableBody, withStyles, ButtonGroup, Button } from "@material-ui/core";
import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
//import * as actions from "../../../../../actions/Staff/addStaff";
import * as companyDataActions from "../../../../../actions/CompanyData/companyData";
//import * as storeActions from "../../../../../actions/Store/stores";
import AddCompanyDataForm from "./AddCompanyDataForm";
import EditIcon from "@material-ui/icons/Edit";
import DeleteIcon from "@material-ui/icons/Delete";
import { useToasts } from "react-toast-notifications";
//import PeopleOutlineTwoToneIcon from '@material-ui/icons/PeopleOutlineTwoTone';
import PageHeader from "../../../PageHeader";
import AddShoppingCartTwoToneIcon from '@material-ui/icons/AddShoppingCartTwoTone';
import ConfirmDialog from "../../../controls/ConfirmDialog";
//import Controls from "./Controls";
import NotListedLocation  from '@material-ui/icons/NotListedLocation';


const styles = theme => ({
    root: {
        "& .MuiTableCell-head": {
            fontSize: "0.95rem"
        }
    },
    paper: {
        margin: theme.spacing(-8.9),
        padding: theme.spacing(6)
    },

    grid:{
        paddingLeft: theme.spacing(2)
    }
})



const AddCompanyData = ({ classes, ...props }) => {
    //Toast message
    const { addToast } = useToasts()
    const [currentId, setCurrentId] = useState(0)
    const [confirmDialog, setConfirmDialog] = useState(false) 
    useEffect(() => {
        props.fetchAllCompanyData()
       // props.fetchAllStores()
    //    console.log(props.companyDataList);
    }, [])

    const showToast= () =>{
        addToast("Record was deleted succesffuly", { appearance: 'error' })
        addToast("Record was deleted succesffuly", { appearance: 'error' })
        addToast("Record was deleted succesffuly", { appearance: 'error' })
        props.fetchAllCompanyData()

    }
    const onDelete = id => {
            props.deleteCompanyData(id, showToast )
            setConfirmDialog(false)
           

    }

    const openInDialog = () => {
        setConfirmDialog(true)
        
        
        
    }

    return (
         <>
         <PageHeader
                title="Company Data"
                //subTitle="Form design with validation"
                icon={<AddShoppingCartTwoToneIcon fontSize="large" />}
            />,
         <Paper className={classes.paper} elevation={3}>
          
                    
                    <AddCompanyDataForm {...({ currentId, setCurrentId })} />
              
                    <div className={classes.grid}>
                    <table class="table  .table-responsive{-sm|-md|-lg|-xl|-xxl} table-bordered table-hover table-sm table-striped" >
                        <thead class="thead-dark">
                            <tr>
                                <th scope="col">Name</th>
                                <th scope="col">Phone</th>
                                <th scope="col">Address</th>
                                <th scope="col">Country</th>
                                <th scope="col">KRA PIN</th>
                                <th scope="col">Email</th>
                                <th scope="col">Website</th>
                                <th scope="col">Registration No</th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>
                           
                            {
                                     props.companyDataList.map((record, index) => {
                                         return ( <tr key = {index} hover>
                                             <td>{record.name}</td>
                                             <td>{record.phone}</td>
                                             <td>{record.address}</td>
                                             <td>{record.country}</td>
                                             <td>{record.krapin}</td>
                                             <td>{record.email}</td>
                                             <td>{record.website}</td>
                                             <td>{record.registrationNo}</td>
                                             {/* <td>{record.category}</td> */}
                                            
                                             
                                             <td>
                                                 <ButtonGroup variant= "text">
                                                    <Button><EditIcon color="primary"
                                                    onClick={()=>{setCurrentId(record.id);}}/></Button>
                                                    <Button><DeleteIcon color="Secondary"
                                                      onClick={() => {
                                                        openInDialog()
                                                        setConfirmDialog({
                                                            onConfirm: () =>{onDelete(record.id)}
                                                        })
                                                        
                                                        //onDelete(record.id)
                                                    }
                                                    }
                                                
                                                    /></Button> 
                                                 </ButtonGroup>
                                             </td>
                                         </tr>

                                         )
                                     })
                                 }
                           
                        </tbody>
                    </table>
                    </div>
                {/* </Grid>
            </Grid> */}
        </Paper>

        <ConfirmDialog 
            title="Are you sure you want to delete this record?"
            subTitle="This operation can't be reversed"
            confirmDialog={confirmDialog}
            setConfirmDialog = {setConfirmDialog}
            />
         </>
    );
}

//export default staff; 

const mapStateToProps = state => ({
    companyDataList: state.companyData.list,
    
})

const mapActionToProps = {
    fetchAllCompanyData: companyDataActions.fetchAllCompanyData,
    deleteCompanyData: companyDataActions.DeleteCompanyData,
    
}

export default connect(mapStateToProps, mapActionToProps)(withStyles(styles)(AddCompanyData));

