import React, { Fragment, useState, useEffect } from "react";
import { FormControl, Grid, TextField, withStyles, InputLabel, Select, MenuItem, Button, FormHelperText } from "@material-ui/core";
import { useForm } from "../../useForm";
import { connect } from "react-redux";
import * as companyDataActions from "../../../../../actions/CompanyData/companyData";

//import * as userLevelActions from "../../../../../actions/UserLevel/userLevel";
import { ToastConsumer, useToasts } from "react-toast-notifications";
import "../../staffForm.css";
import { RestoreSharp } from "@material-ui/icons";


const styles = theme => ({
    root: {
        '& .MuiTextField-root': {
            margin: theme.spacing(1),
            minWidth: 230,
        }
    },

    formControl: {
        margin: theme.spacing(1),
        minWidth: 230,
    },
    smMargin: {
        margin: theme.spacing(1),

    },
})

const defaultImageSrc = './images/icon-placeholder-5.jpg'
const initialFieldValues = {
    name: "",
    logo: "",
    phone: "",
    address: "",
    country: "",
    krapin: "",
    email: "",
    registrationNo: "",
    website: "",
    imageName: "",
    imageSrc: defaultImageSrc,
    imageFile: null
}






const AddCompanyDataForm = ({ classes, ...props }) => {

    //toast message
    const { addToast } = useToasts()
    const showPreview = e => {
        if (e.target.files && e.target.files[0]) {
            let imageFile = e.target.files[0];
            const reader = new FileReader();
            reader.onload = x => {
                setValues({
                    ...values,
                    imageFile,
                    imageSrc: x.target.result
                })
            }
            reader.readAsDataURL(imageFile)
        } else {
            setValues({
                ...values,
                imageFile: null,
                imageSrc: defaultImageSrc
            })
        }
    }
    //realtime validation for the whole form controls
    const validate = (fieldValues = values) => {
        let temp = { ...errors }
        if ('name' in fieldValues) {
            temp.name = fieldValues.name ? "" : "This field is required"
            // addToast("User data saved succeffuly", {appearance:'success'})
            // console.log("Tast has beeen displayed")
        }


        if ('phone' in fieldValues) {
            temp.phone = fieldValues.phone ? "" : "This field is required"
        }


        if ('address' in fieldValues) {
            temp.address = fieldValues.address ? "" : "This field is required"
        }


        if ('country' in fieldValues) {
            temp.country = fieldValues.country ? "" : "This field is required"
        }

        if ('krapin' in fieldValues) {
            temp.krapin = fieldValues.krapin ? "" : "This field is required"
        }
        if ('email' in fieldValues) {
            temp.email = fieldValues.email ? "" : "This field is required"
        }
        if ('website' in fieldValues) {
            temp.website = fieldValues.website ? "" : "This field is required"
        }
        if ('registrationNo' in fieldValues) {
            temp.registrationNo = fieldValues.registrationNo ? "" : "This field is required"
        }



        setErrors({
            ...temp
        })
        if (fieldValues == values) {
            return Object.values(temp).every(x => x == "")
        }


    }

    const [
        values,
        setValues,
        errors,
        setErrors,
        handleInputChange,
        resetForm
    ] = useForm(initialFieldValues, validate, props.setCurrentId);

    const showToast = () => {
        addToast("Company data saved succeffuly", { appearance: 'success' })
        addToast("Company data saved succeffuly", { appearance: 'success' })
        addToast("Company data saved succeffuly", { appearance: 'success' })
        props.fetchAllCompanyData();
    }



    const handleSubmit = e => {
        e.preventDefault()


        if (validate()) {

            const onSuccess = () => {
                showToast();
                resetForm();


            }



            if (props.currentId == 0) {

                //using toast notification
                values.logo = values.imageSrc;
                props.createCompanyData(values, onSuccess)
                //addorEdit(values, onSuccess)

            }
            else {
                //using windows alert notification
                //props.updateDCandidate(props.currentId, values, () => {window.alert('updated')})
                //using toast notification
                // console.log(values);
                values.logo = values.imageSrc;
                console.log(values.logo);
                props.updateCompanyData(props.currentId, values, onSuccess)



            }

            if (props.openPopup == true) {
                props.setOpenPopup(false)
            }

        }


    }

    useEffect(() => {
        props.fetchAllCompanyData();

        if (props.currentId != 0)
            setValues({
                ...props.companyDataList.find(x => x.id == props.currentId)
            })
        setErrors({})


        // props.fetchAllCategory();



    }, [props.currentId])





    return (
        <section className="content">
            <div className="container-fluid">
                <div className="row">
                    {/* left column */}
                    <div className="col-md-12">
                        {/* jquery validation */}
                        <div className="card card-primary">
                            <div className="card-header">
                                <h4 className="card-title">Add Company Data<small> </small></h4>
                            </div>
                            {/* /.card-header */}
                            {/* form start */}
                            <form id="quickForm" autoComplete="off" noValidate className={classes.root} onSubmit={handleSubmit}>

                                <TextField
                                    className="form-ctr"
                                    id="standard-basic"
                                    type="text"
                                    name="name"
                                    label="Name"
                                    value={values.name}
                                    onChange={handleInputChange}
                                    {...(errors.name && { error: true, helperText: errors.name })}
                                />

                                <TextField
                                    className="form-ctr"
                                    id="standard-basic"
                                    type="text"
                                    name="phone"
                                    label="Phone"
                                    value={values.phone}
                                    onChange={handleInputChange}
                                    {...(errors.phone && { error: true, helperText: errors.phone })}
                                />

                                <TextField
                                    className="form-ctr"
                                    id="standard-basic"
                                    type="text"
                                    name="address"
                                    label="Address"
                                    value={values.address}
                                    onChange={handleInputChange}
                                    {...(errors.address && { error: true, helperText: errors.address })}
                                />

                                <TextField
                                    className="form-ctr"
                                    id="standard-basic"
                                    type="text"
                                    name="country"
                                    label="Country"
                                    value={values.country}
                                    onChange={handleInputChange}
                                    {...(errors.country && { error: true, helperText: errors.country })}
                                />
                                <TextField
                                    className="form-ctr"
                                    id="standard-basic"
                                    type="text"
                                    name="krapin"
                                    label="KRA PIN"
                                    value={values.krapin}
                                    onChange={handleInputChange}
                                    {...(errors.krapin && { error: true, helperText: errors.krapin })}
                                />
                                <TextField
                                    className="form-ctr"
                                    id="standard-basic"
                                    type="text"
                                    name="email"
                                    label="Email"
                                    value={values.email}
                                    onChange={handleInputChange}
                                    {...(errors.email && { error: true, helperText: errors.email })}
                                />
                                <TextField
                                    className="form-ctr"
                                    id="standard-basic"
                                    type="text"
                                    name="website"
                                    label="Website"
                                    value={values.website}
                                    onChange={handleInputChange}
                                    {...(errors.website && { error: true, helperText: errors.website })}
                                />
                                <TextField
                                    className="form-ctr"
                                    id="standard-basic"
                                    type="text"
                                    name="registrationNo"
                                    label="Registration No"
                                    value={values.registrationNo}
                                    onChange={handleInputChange}
                                    {...(errors.registrationNo && { error: true, helperText: errors.registrationNo })}
                                />

                                <div className="form-ctr">
                                    <span>Company Logo</span><br />
                                    <img src={values.imageSrc} className="card-img-top" style={{ width: 100, height: 100 }} />
                                    <input type="file" accept="image/*" className="form-control-file"
                                        onChange={showPreview}
                                    />
                                </div>


                                {/* /.card-body */}
                                <div className="card-footer">
                                    <div>
                                        <button type="submit" className="btn btn-primary" >Save</button>
                                        <button variant="contained"
                                            className="btn btn-secondary"
                                            onClick={resetForm} style={{ marginLeft: 3 }}>Reset</button>
                                    </div>

                                </div>

                            </form>

                        </div>
                        {/* /.card */}
                    </div>
                    {/*/.col (left) */}
                    {/* right column */}
                    <div className="col-md-6">
                    </div>
                    {/*/.col (right) */}
                </div>
                {/* /.row */}
            </div>{/* /.container-fluid */}
        </section>

    );
}


const mapStateToProps = state => ({
    companyDataList: state.companyData.list,
    // categoryList: state.category.list
})

const mapActionToProps = {
    fetchAllCompanyData: companyDataActions.fetchAllCompanyData,
    deleteCompanyData: companyDataActions.DeleteCompanyData,
    // fetchAllCategory: categActions.fetchAllCategory,
    createCompanyData: companyDataActions.createCompanyData,
    updateCompanyData: companyDataActions.updateCompanyData,



}
export default connect(mapStateToProps, mapActionToProps)(withStyles(styles)(AddCompanyDataForm));